import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
const originalPush = Router.prototype.push;
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
};
const router = new Router({
  routes: [
    {
      path: '/',
      component:()=>import("../components/Index.vue"),
      //路由重定向
      redirect: '/login',
      children: [
        {
          path: '/home',
          component: ()=>import("../components/Home.vue"),
          children: [
            {
              path: '/home/trainticket',
              component: ()=>import("../components/Home/TrainTicket.vue"),
            }
          ]
        },
        {
          path:'/login',
          component:()=>import("../components/Login.vue"),
        },
        {
          path:'/register',
          component:()=>import("../components/Register.vue"),
        },
        {
          path: '/grabvotes',
          component: ()=>import("../components/GrabVotes.vue"),
        },
        {
          path: '/order',
          component: ()=>import("../components/Order.vue"),
        },
        {
          path: '/my',
          component:()=>import("../components/my.vue"),
        }
      ]
    },
    {
      path: '/selectcity',
      component:()=>import("../components/SelectCity.vue"),
    },
    {
      path: '/showcalendarinput',
      component:()=>import("../components/calendar/show-calendar-input.vue"),
    },
    {
      path: '/showtickets',
      component:()=>import("../components/showTickets.vue"),
    },
    {
      path: '/buyticket',
      name: 'buyticket',
      component:()=>import("../components/buyTicket.vue"),
    },
    {
      path: '/orderdetails/:id',
      name: 'orderdetails',
      component:()=>import("../components/orderDetails.vue"),
    },
    {
      path: '/ticketbooking',
      name: 'ticketbooking',
      component:()=>import("../components/ticketBooking.vue"),
    },
    {
      path: '/passenger',
      name: 'passenger',
      component: ()=>import("../components/passenger.vue"),
    }
  ]
})
//router.push('/home');
//router.push('/home')
export default router